import { Component, Prop, h, Host } from '@stencil/core';
import { classlist } from '../../utils/classlist';

export type AvatarSize = 'sm' | 'md' | 'lg';
export type AvatarVariant = 'primary' | 'secondary' | 'muted';

const avatarVariantsConfig = {
    variants: {
        size: {
            sm: 'h-24 w-24 text-xs',
            md: 'h-32 w-32 text-sm',
            lg: 'h-40 w-40 text-base',
        },
        variant: {
            primary: 'bg-primary text-primary-foreground',
            secondary: 'bg-secondary text-secondary-foreground',
            muted: 'bg-muted text-muted-foreground',
        },
    },
    defaultVariants: {
        size: 'md',
        variant: 'primary',
    },
};

/**
 * @category Decoration
 * @description Purely decorative avatar component that displays user profile images or initials. Accessibility attributes should be added to the parent element if needed.
 */
@Component({
    tag: 'at-avatar',
    shadow: false,
})
export class AtAvatar {
    /**
     * URL for the avatar image
     *
     * Recommended cropped image sizes for optimal display:
     * - sm:24x24px
     * - md:32x32px
     * - lg:40x40px
     *
     * Higher resolution images (2x display size) are recommended for crisp display on high-DPI screens.
     */
    @Prop() src?: string;

    /**
     * Alt text for the avatar image
     */
    @Prop() alt?: string;

    /**
     * Initials text to display when no image is provided
     */
    @Prop() initials?: string;

    /**
     * Size of the avatar
     */
    @Prop() size: AvatarSize = 'md';

    /**
     * Visual variant of the avatar
     */
    @Prop() variant: AvatarVariant = 'secondary';

    render() {
        const getClasses = classlist(
            'flex shrink-0 items-center justify-center overflow-hidden rounded-full font-medium select-none',
            avatarVariantsConfig,
        );

        const classes = getClasses({
            size: this.size,
            variant: this.variant,
        });

        return (
            <Host
                class={classes}
                data-name="avatar-container"
            >
                {this.src && (
                    <img
                        src={this.src}
                        alt={this.alt || 'Avatar'}
                        class="h-full w-full object-cover"
                        data-name="avatar-image"
                    />
                )}
                {!this.src && this.initials && (
                    <span data-name="avatar-initials">{this.initials}</span>
                )}
                <slot />
            </Host>
        );
    }
}
